import React from "react";
import s from './Nav.module.scss'
import {Link} from "react-router-dom";


function Nav() {
    return (
        <div className={s.nav}>
            {/*<NavLink to={'/'} className={isActive=>isActive?s.active:s.primary}></NavLink>*/}
            {/*<NavLink to={'/skills'} className={isActive=>isActive?s.active:s.primary}></NavLink>*/}
            {/*<NavLink to={'/works'} style={isActive=>({color: isActive?'#ffb400':'#fff'}) }></NavLink>*/}
            {/*<NavLink to={'/contacts'} style={isActive=>({color: isActive?'#ffb400':'#fff'}) }></NavLink>*/}
            <Link to={'/'}></Link>
            <Link to={'/skills'}></Link>
            <Link to={'/works'}></Link>
            <Link to={'/contacts'}></Link>
        </div>
    );
}

export default Nav;